@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
* {
  font-family: "Poppins", sans-serif;
}
.about {
  display: flex;
  justify-content: center;
  align-items: center;

  transition: 1s background;
  background: url("../assets/LogoAll.PNG");
  background-color: black;
  /* background: linear-gradient(
    332deg,
    rgb(255, 115, 0) 0%,
    rgb(145, 68, 128) 100%
  ); */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  animation: kenan 45s 3s ;
}
@keyframes kenan {
  0% {
    background: url("../assets/bg1.PNG");

    /* background-size: cover; */
    background-position: center center;
  }
  25% {
    background: url("../assets/bg2.PNG");

    /* background-size: cover; */
    background-position: center center;
  }
  50% {
    background: url("../assets/bg3.PNG");

    /* background-size: cover; */
    background-position: center center;
  }
  75% {
    background: url("../assets/P3.PNG");

    /* background-size: cover; */
    background-position: center center;
  }
  100% {
    background: url("../assets/LogoAll.PNG");
    background-color: black;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }
}


.about1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 90vh;
}
.about2 {
  margin: 15px;

  color: rgb(255, 255, 255);

  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 90vh;
  flex-direction: column;
}
.about2 h1 {
  font-size: 2rem;
  font-weight: bold;
}

.about2 p {
  color: black;
  text-align: center;
  padding: 10px;

  background: lightblue;
  border-radius: 10px;
  font-size: 1.2rem;
}
.mission {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  background: url("../assets/bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.content p {
  line-height: 2;
  color: black;

  font-size: 15px;
}
.logo {
  animation: logo 10s alternate 2s infinite;
}
@keyframes logo {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.team {
  height: 65vh;
  background: lightgrey;
}

.teamAbout p {
  text-align: center;
  line-height: 2;
  font-size: 17px;
}

.teamPhoto {
  height: 100%;

  width: 50%;
}

.cool {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
